.notification {
  border-radius: 15px;
  border: 4px solid green;
  padding: 10px;
  width: 700px;
  height: 20px;
  color: green;
  background: lightgray;
  font-size: 20px;
  margin-bottom: 5px;
}

.error {
  border-radius: 15px;
  padding: 10px;
  border: 4px solid red;
  width: 700px;
  height: 20px;
  color: red;
  background: lightgray;
  font-size: 20px;
  margin-bottom: 5px;
}